import React from 'react';
import Card from './Card';

const CardList = ( { robots } ) => {
  console.log('cardlist');
  return (
    <div>
      {
        robots.map((robot, id) => {
          return <Card 
          key={id}
          id={robot.id}
          name={robot.name} 
          email= {robot.email} 
          />
        })
      }
    </div>
  );
}

export default CardList;


